<h1 id="welcome" tabindex="-1"><a class="header-anchor" href="#welcome" aria-hidden="true">#</a> Welcome!</h1>
<p>Welcome to the course <em>Android Development</em>! In this course you'll learn how to create Android applications.</p>
<p>This website will be used to host the course material only. Canvas will primarily be used to:</p>
<ul>
<li>Send info to you through announcements (be sure to have your global notification settings or your course notification settings enabled)</li>
<li>Organize you into lab groups (see the People page)</li>
<li>Keep track of your progress in the course (passed lab assignments) (see the Modules page and the Grades page)</li>
</ul>
<p>Read the <RouterLink to="/courses/android-development/study-guide/">Study Guide</RouterLink> for the details.</p>
<div class="custom-container warning"><p class="custom-container-title">Swedish VS International Students</p>
<p>The Swedish students from the following programs:</p>
<ul>
<li>Software Engineering and Mobile Platforms</li>
<li>Embedded Systems</li>
</ul>
<p>Will in the next study period take the course <a href="https://ju.se/studera/kurser.html?courseCode=TGYG19&amp;semester=20191&amp;revision=1,000&amp;lang=sv" target="_blank" rel="noopener noreferrer">Gruppdynamik (TGYG19, 3 Credits)<OutboundLink/></a>. That course is about reflecting upon and improving your skills of working in a group, and for that course you should have a diary describing your experiences from working in a group. That diary is something you will produce in this course as part of your project work.</p>
<p>More information about this later, but what's important for you to know now is that:</p>
<ul>
<li>Swedish students will not work with international students in this course (the project work groups you use in this course will be used in the Gruppdynamik course as well).</li>
<li>Swedish students failing to participate in the project work in this course will have a hard time passing the Gruppdynamik course since they won't have a diary of good enough quality to use in that course.</li>
</ul>
</div>
<h2 id="information-to-old-students" tabindex="-1"><a class="header-anchor" href="#information-to-old-students" aria-hidden="true">#</a> Information to old students</h2>
<p>If you're an old student that took this course a previous year but still have some examination tests left to pass you take those tests as they are described this year. The old Canvas course won't be used any more, so join this year's Canvas course, so we can keep track of your progress in the course this year there. If you aren't registered for the course this year, ask the course coordinator to invite you to the Canvas course by sending him an email message based on the template in <FigureNumber /> below with the topic <code>Invitation to Canvas Course</code> to <code>peter.larsson-green@ju.se</code>.</p>
